import React, { Component } from 'react';


/*
    sizes: large | full-width | other

    <Button size="large" isPrimary disabled />
*/

class Button extends Component {
    constructor(props){
        super(props);
        this.props = {
            size: '',
            isPrimary: true,
            disabled: false
        };
        this.getCssName = this.getCssName.bind(this);
    }

    getCssName(){
        if(this.props.disabled && this.props.isPrimary){
            return 'disabledPrimary';
        }
        if(this.props.disabled && !this.props.isPrimary){
            return 'disabledSecondary';
        }

        if(this.props.isPrimary){
            return 'primary';   
        }

        if(this.props.size === 'large'){
            return 'large';
        }
        if(this.props.size === 'full-width'){
            return 'fullWidth';
        }
    }

    render(){
        return <button className={this.getCssName} disabled={this.props.disabled} />
    }
}

export default Button;



